.input {
  padding: 6px 10px;
  outline: 0;
  border: 0;
  width: 100%;
  font-size: 1.2rem;
  background-color: rgba(0, 0, 0, 0.336);
  color: #f0f0f0;
  backdrop-filter: blur(3px);
}

.input::placeholder {
  color: #f0f0f0;
}

.input:focus {
  border-color: var(--primary-color);
}
